@import '~antd/dist/antd.less';

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  height: 100%;
}

.mockstar-container.ant-layout {
  min-height: 100%;
  background: #f0f2f5;

  & > .ant-breadcrumb {
    margin: 16px 0;
  }

  & > .ant-layout-content {
    padding: 24px;
    margin: 0;
    min-height: 500px;
  }
}

@primary-color: #1DA57A;